<template>
  <div class="river">
    <div class="setUp" >
      <div class="setLeft">
        <div class="back">
          <span @click="back"><i class="mintui mintui-back"></i></span>
        </div>
        <div class="tabs">
          <a :class="{selected:active === 'tab-container1'}" @click="active = 'tab-container1'">三江榜</a>
          <a :class="{selected:active === 'tab-container2'}" @click="active = 'tab-container2'" @click.once="getBooks">青云榜</a>
        </div>

      </div>
    </div>
    <div class="page-tab-container">
      <mt-tab-container class="page-tabbar-tab-container" v-model="active" swipeable>
        <mt-tab-container-item id="tab-container1" :style="screen" @scroll.native="handleScroll" >
          <div class="tabs-container" id="doc" v-show="active === 'tab-container1'">
            <div class="tab">
              <div class="time">
              04月08日 - 04月15
              </div>
              <div class="contents">
                <div class="content" v-for="(b,index) in bookList">
                  <div class="syn">
                    {{b.synopsis}}
                  </div>
                  <div class="books">
                    <div class="bLeft">
                       <img :src="b.path" class="xImg">
                    </div>
                    <div class="sug ef">
                      <div class="">{{b.name}}</div>
                      <div class="word lc">{{b.synopsis}}</div>
                      <div class="wsp lc">
                        <span>
                          <i class="mintui mintui-zuozhe"></i>{{b.author}}
                        </span>
                        <span class="sca">
                          <span>{{b.type | typeFilter}}</span>
                          <span>连载</span>
                          <span>25万字</span>
                        </span>
                      </div>
                    </div>
                  </div>
                  <div class="ope">
                    <a>免费试读</a>
                    <a>加入书架</a>
                  </div>
                </div>
              </div>
              <div class="time">
                04月01日 - 04月8日
                </div>
                <div class="contents">
                  <div class="content" v-for="(b,index) in bookList">
                    <div class="syn">
                      {{b.synopsis}}
                    </div>
                    <div class="books">
                      <div class="bLeft">
                         <img :src="b.path" class="xImg">
                      </div>
                      <div class="sug ef">
                        <div class="">{{b.name}}</div>
                        <div class="word lc">{{b.synopsis}}</div>
                        <div class="wsp lc">
                          <span>
                            <i class="mintui mintui-zuozhe"></i>{{b.author}}
                          </span>
                          <span class="sca">
                            <span>{{b.type | typeFilter}}</span>
                            <span>连载</span>
                            <span>25万字</span>
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="ope">
                      <a>免费试读</a>
                      <a>加入书架</a>
                    </div>
                  </div>
                </div>
            </div>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-container2" :style="screen">
           <div class="tabs-container" v-show="active === 'tab-container2'">
              <div class="tab">
                <div class="time">
                04月08日 - 04月15日
                </div>
                <div class="contents">
                  <div class="content" v-for="(b,index) in bookList1">
                    <div class="syn">
                      {{b.synopsis}}
                    </div>
                    <div class="books">
                      <div class="bLeft">
                         <img :src="b.path" class="xImg">
                      </div>
                      <div class="sug ef">
                        <div class="">{{b.name}}</div>
                        <div class="word lc">{{b.synopsis}}</div>
                        <div class="wsp lc">
                          <span>
                            <i class="mintui mintui-zuozhe"></i>{{b.author}}
                          </span>
                          <span class="sca">
                            <span>{{b.type | typeFilter}}</span>
                            <span>连载</span>
                            <span>25万字</span>
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="ope">
                      <a>免费试读</a>
                      <a>加入书架</a>
                    </div>
                  </div>
                </div>
                <div class="time">
                04月01日 - 04月8日
                </div>
                <div class="contents">
                  <div class="content" v-for="(b,index) in bookList1">
                    <div class="syn">
                      {{b.synopsis}}
                    </div>
                    <div class="books">
                      <div class="bLeft">
                         <img :src="b.path" class="xImg">
                      </div>
                      <div class="sug ef">
                        <div class="">{{b.name}}</div>
                        <div class="word lc">{{b.synopsis}}</div>
                        <div class="wsp lc">
                          <span>
                            <i class="mintui mintui-zuozhe"></i>{{b.author}}
                          </span>
                          <span class="sca">
                            <span>{{b.type | typeFilter}}</span>
                            <span>连载</span>
                            <span>25万字</span>
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="ope">
                      <a>免费试读</a>
                      <a>加入书架</a>
                    </div>
                  </div>
                </div>
              </div>
           </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>
<script>
  import {Indicator} from 'mint-ui';
  export default {
    name:'river',
    data(){
      return {
        active:'tab-container1',
        swipeable:true,
        bookList:[],
        bookList1:[],
        screen:{
          'height':'',
          'overflow':'auto',
          'z-index':1000
        }
      }
    },
    methods:{
      back(){
        this.$router.go(-1);
      },
      getBooks(){
        Indicator.open('加载中');
        this.$axios.post('/vueBook/bookList',{})
        .then((res)=>{
          if(res.data.state === 200){
            Indicator.close();
            if(this.active === 'tab-container1'){
              this.bookList = [...this.bookList,...res.data.data]
            }else {
              this.bookList1 = [...this.bookList1,...res.data.data]
            }
            // this.bookList = res.data.data;
          }
        })
        .catch((err)=>{
          console.log(err)
        });
      },
      handleScroll(){
        var d = document.getElementById('doc');
        var t = d.getElementsByClassName('time');
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        var offsetTop = document.querySelector('.time').offsetTop


         console.log()
          console.log(window.screen.height);
          console.log(document.documentElement.offsetHeight)
          // console.log(offsetTop)
      }
    },
    created(){
      // this.screen.height = (window.screen.height - 40) + 'px';
      this.getBooks();
    },
    filters:{
      typeFilter:function(value){
        return value.substring(2);
      }
    },
    mounted () {

    },
  }

</script>
<style scoped>
  .river {
    background-color: #ccc;
  }
</style>


